<template>
  <v-container class="d-flex justify-center">
    <v-card style="max-width: 320px" tile>
      <v-img
        height="175"
        src="https://cdn.vuetifyjs.com/images/cards/road.jpg"
      ></v-img>
      <div style="position: relative">
        <v-avatar
          :size="100"
          color="rgba(0,0,0,.8)"
          style="position: absolute; left: 35%; bottom: -40px"
        >
          <v-icon color="white" size="90" style="opacity: 1"
            >mdi-account</v-icon
          >
        </v-avatar>
      </div>
      <v-card-text class="mt-3">
        <v-card-title>
          {{ user.username }}
        </v-card-title>
        <v-card-subtitle>
          {{ $route.params.mapel.split("-").join(" ") }}
        </v-card-subtitle>
        <v-card-actions>
          <v-btn block @click="absen" color="primary"
            >Absen!
            <br />
            {{ new Date().toLocaleString() }}
          </v-btn>
        </v-card-actions>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import haversine from "haversine-distance";
export default {
  props: ["user"],
  data() {
    return {
      currentLocation: {
        lat: 0,
        lng: 0,
      },
    };
  },
  methods: {
    absen() {
      const distance =
        haversine(this.currentLocation, {
          lat: this.user.lat,
          lng: this.user.lng,
        }) / 1000;
      if (distance > 1.1) {
        this.bus.$emit("showSnackbar", {
          message: "Absen Gagal harus berada di radius 1km dari rumah",
          show: true,
          color: "error",
        });
      } else {
        this.bus.$emit("showSnackbar", {
          message: `Absen pada tanggal ${new Date().toLocaleString()} Berhasil`,
          show: true,
        });
      }
    },
  },
  mounted() {
    this.$getLocation({
      enableHighAccuracy: true,
    })
      .then(({ lat, lng }) => {
        this.currentLocation.lat = lat;
        this.currentLocation.lng = lng;
      })
      .catch(() => {
        this.bus.$emit("showSnackbar", {
          message: "Please Allow Location",
          show: true,
          color: "error",
        });
      });
  },
};
</script>

